import { Injectable } from '@angular/core';
import { HttpClient , HttpHeaders} from '@angular/common/http';
import { Observable,Subscriber } from 'rxjs';
import { baseUrl } from '../../Models/common';
import { ProductDetails, AllProductResponse } from '../../Models/product-models';

@Injectable({
  providedIn: 'root'
})
export class ProductService {
  productDetails:any;

  constructor(private http:HttpClient) { }

  getMethod(successCallBack:Function,errorCallBack:Function,options:any,endPoint:string):void{
    let x = {};
    let queryParameter = '';
    let keysList = Object.keys(options);
    keysList.forEach(key=>{
      // queryParameter = queryParameter + key.toString+"="+options[key].toString+"&";

      queryParameter = queryParameter + key + '=' + options[key] + '&';
      console.log(queryParameter);
    });
    queryParameter = queryParameter.slice(0,queryParameter.length-1);
    // console.log(queryParameter);
    console.log(baseUrl +  endPoint + '?'  + queryParameter);
    this.http.get(baseUrl +  endPoint + '?'  + queryParameter).subscribe(
      (response:any) => {
        console.log(">>>>>");
        this.productDetails = response.data;
        successCallBack(response);
    },
    (error)=>{
      // errorCallBack(error);
    });

  }

  postMethod(successCallBack:Function,errorCallback:Function,body:any,url:any){

    this.http.post(baseUrl+url,body).subscribe((response)=>{
      successCallBack(response);
    },(error)=>{
      errorCallback(error);
    });

  }

  deleteMethod(successCallBack:Function,errorCallback:Function,body:any,url:any){
    console.log(body);
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json' }), body: body
  };
    this.http.delete(baseUrl+url,httpOptions).subscribe((response)=>{
      successCallBack(response);
    },(error)=>{
      errorCallback(error);
    });
  }

}

